export class ExtendableException extends Error {

    constructor(msg = '') {

        super(msg);
        this.name = this.constructor.name;
    }
} 

const exception = ExtendableException;

export function isExtendableException(err) {

    return err instanceof exception || Object.getPrototypeOf(err) instanceof exception 
}

export function getExceptionMessage(err) {
    
    if(isExtendableException(err)) {
        return err.message;
    } else {
        console.log(err);
        return 'Não foi possível realizar a operação.';
    }
}